// AddData2.js
import React, { useState } from 'react';
import axios from "axios";
import toast from "react-hot-toast";

const AddData1 = () => {
    const [formData, setFormData] = useState({
        //   
        value: '',
        label: '',

    });

    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            await axios.post("http://193.57.210.140:9000/api/add2", formData); //  URL    
            toast.success("  ");
        } catch (error) {
            console.error(error);
            toast.error("       ");
        }
    };

    return (
        <div>
            <h2> </h2>
            <form onSubmit={handleSubmit}>
                {/*    */}
                <input type="text" name="value" value={formData.value} onChange={handleChange} />
                <input type="text" name="label" value={formData.label} onChange={handleChange} />
                <button type="submit" className="hh"></button>
            </form>
        </div>
    );
};

export default AddData1;
