import React, { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import Select from "react-select";
import "../adduser/add.css";
import toast from "react-hot-toast";
import MyComponent from "../AddData/MyComponent";
import MyComponent1 from "../AddData/MyComponent1";

const Edit = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [user, setUser] = useState({});
  const [achievements1, setAchievements1] = useState({});
  const [selectedOption1, setSelectedOption1] = useState(null);
  const [selectedOption2, setSelectedOption2] = useState(null);
  const [selectedOption3, setSelectedOption3] = useState(null);
  const [selectedOption4, setSelectedOption4] = useState(null);
  const [selectedOption5, setSelectedOption5] = useState(null);

  const inputChangeHandler = (e) => {
    const { name, value } = e.target;
    setUser({ ...user, [name]: value });
  };

  useEffect(() => {
    axios
      .get(`http://193.57.210.140:8000/api/getone/${id}`)
      .then((response) => {
        console.log(response);
        setUser({ ...response.data });
      })
      .catch((error) => {
        console.log(error);
      });
  }, [id]);

  const submitForm = async (e) => {
    e.preventDefault();
    const achievementsUpdate = {
      maktab: achievements1.maktab || null,
      ozmun: achievements1.ozmun || null,
      other1: selectedOption1 ? selectedOption1.label : null,
      other2: selectedOption2 ? selectedOption2.label : null,
      other3: selectedOption3 ? selectedOption3.label : null,
      other4: selectedOption4 ? selectedOption4.label : null,
      other5: selectedOption5 ? selectedOption5.label : null,
    };

    const userData = {
      ...user,
      achievements: [...(user.achievements || []), achievementsUpdate],
    };

    console.log("Data to be sent to the backend:", userData); //    
    await axios
      .put(`http://193.57.210.140:8000/api/update/${id}`, userData)
      .then((response) => {
        toast.success(response.data.msg, { position: "top-right" });
        navigate("/");
      })
      .catch((error) => console.log(error));
  };

  const options = [
    { value: "", label: "" },
    { value: "", label: "" },
    { value: "", label: "" },
    { value: "", label: "" },
    { value: "", label: "" },
    { value: "", label: "" },
  ];
  const options1 = [
    { value: " ", label: " " },
    { value: "     ", label: "    " },
    { value: "  ", label: " " },
    { value: "   ", label: "   " },
    { value: " ", label: " " },
    { value: "", label: "" },
    { value: "  ", label: "" },
  ];
  const options2 = [
    { value: "5-6 ", label: "5-6 " },
    { value: "7-8  ", label: "7-8 " },
    { value: "9-10 ", label: "9-10" },
    { value: "9-10()", label: "9-10()" },
    { value: "11", label: "11" },
  ];
  const options3 = [
    { value: "", label: "" },
    { value: "", label: "" },
    { value: "", label: "" },
  ];
  const options4 = [
    { value: "", label: "" },
    { value: "Spealing bee ", label: "Spealing bee " },
  ];

  return (
    <div className="addUser">
      <Link to={"/"} className={"addButton"}>
         
      </Link>
      <h3></h3>
      <form className="addUserForm" onSubmit={submitForm}>
        <div className="inputGroup">
          <label htmlFor="fname"></label>
          <input
            type="text"
            value={user.fname || ""}
            onChange={inputChangeHandler}
            id="fname"
            name="fname"
            autoComplete="off"
            placeholder=""
          />
        </div>
        <div className="inputGroup">
          <label htmlFor="lname"></label>
          <input
            type="text"
            value={user.lname || ""}
            onChange={inputChangeHandler}
            id="lname"
            name="lname"
            autoComplete="off"
            placeholder=""
          />
        </div>
        <div className="inputGroup">
          <label htmlFor="email"></label>
          <input
            type="text"
            value={user.email || ""}
            onChange={inputChangeHandler}
            id="email"
            name="email"
            autoComplete="off"
            placeholder="Email"
          />
        </div>
        <label htmlFor="select1"></label>
        <MyComponent
          onSelectChange={(selectedOption) => {
            setAchievements1((prev) => ({
              ...prev,
              maktab: selectedOption.label,
            }));
          }}
        />
        <label htmlFor="select1"></label>
        <MyComponent1
          onSelectChange={(selectedOption) => {
            setAchievements1((prev) => ({
              ...prev,
              ozmun: selectedOption.label,
            }));
          }}
        />
        <div className="inputGroup">
          <label htmlFor="select1">    </label>
          <Select
            id="select1"
            value={selectedOption1}
            onChange={setSelectedOption1}
            options={options}
            placeholder=" "
            isSearchable
          />
        </div>
        <div className="inputGroup">
          <label htmlFor="select2">  </label>
          <Select
            id="select2"
            value={selectedOption2}
            onChange={setSelectedOption2}
            options={options1}
            placeholder=" "
            isSearchable
          />
        </div>
        <div className="inputGroup">
          <label htmlFor="select3">   :</label>
          <Select
            id="select3"
            value={selectedOption3}
            onChange={setSelectedOption3}
            options={options2}
            placeholder=""
            isSearchable
          />
        </div>
        <div className="inputGroup">
          <label htmlFor="select4"> </label>
          <Select
            id="select4"
            value={selectedOption4}
            onChange={setSelectedOption4}
            options={options3}
            placeholder=" "
            isSearchable
          />
        </div>
        <div className="inputGroup">
          <label htmlFor="select5"></label>
          <Select
            id="select5"
            value={selectedOption5}
            onChange={setSelectedOption5}
            options={options4}
            placeholder=""
            isSearchable
          />
        </div>
        <div className="inputGroup">
          <button type="submit"></button>
        </div>
      </form>
    </div>
  );
};

export default Edit;
